"use client";
import React, { useEffect, useState, useRef } from "react";
import {
  Calendar,
  CheckCircle,
  ArrowRight,
  ArrowLeft,
  User,
  Mail,
  Phone as PhoneIcon,
  Stethoscope,
  CreditCard,
  AlertCircle,
} from "lucide-react";

// Imágenes (no tocar)
import iconLocation from "../img/Asset 12.png";
import iconPhone from "../img/Asset 9.png";
import iconEmail from "../img/Asset 10.png";

export default function Contact() {
  const rootRef = useRef(null);

  // Animaciones de aparición
  useEffect(() => {
    const root = rootRef.current;
    if (!root || !("IntersectionObserver" in window)) return;
    const els = Array.from(root.querySelectorAll(".msf-anim"));
    const io = new IntersectionObserver(
      (entries, obs) => {
        entries.forEach((e) => {
          if (e.isIntersecting) {
            e.target.classList.add("is-inview");
            obs.unobserve(e.target);
          }
        });
      },
      { threshold: 0.18, rootMargin: "0px 0px -10% 0px" }
    );
    els.forEach((el) => io.observe(el));
    return () => io.disconnect();
  }, []);

  return (
    <section ref={rootRef} className="section contact" style={{ paddingBottom: "4rem" }}>
      <div className="container">
        <header
          className="msf-anim msf-up"
          style={{ textAlign: "center", marginBottom: "1.15rem" }}
        >
          <h2 className="h2-contact">Agende su Consulta</h2>
          <p className="lead" style={{ maxWidth: "68ch", margin: "0 auto" }}>
            Estamos listos para responder a sus preguntas y ayudarle a dar el
            siguiente paso. Complete el formulario para agendar su cita.
          </p>
        </header>

        <div
          className="contact__grid"
          style={{
            display: "grid",
            gridTemplateColumns: "minmax(280px, 1fr) 360px",
            gap: "1.5rem",
            alignItems: "start",
          }}
        >
          {/* LEFT: Multi-step form */}
          <div
            className="contact__card msf-anim msf-left"
            style={{
              background: "var(--surface-1, var(--onix-quirurgico, #171F21))",
              border: "1px solid var(--divider, rgba(255,255,255,.06))",
              borderRadius: "16px",
              padding: "1.25rem",
              boxShadow: "var(--shadow, 0 8px 30px rgba(0,0,0,.35))",
            }}
          >
            <MultiStepForm />
          </div>

          {/* RIGHT: CONTACT INFO */}
          <aside
            className="contact__info msf-anim msf-right"
            style={{
              background: "var(--surface-1, var(--onix-quirurgico, #171F21))",
              border: "1px solid var(--divider, rgba(255,255,255,.06))",
              borderRadius: "12px",
              padding: "1rem",
              display: "flex",
              flexDirection: "column",
              gap: "1rem",
            }}
          >
            <h3 className="h3" style={{ margin: 0 }}>
              Información de Contacto
            </h3>

            <div
              className="contact__item"
              style={{ display: "flex", gap: ".75rem", alignItems: "flex-start" }}
            >
              <div className="contact__icon" aria-hidden>
                <img
                  src={iconLocation}
                  alt=""
                  aria-hidden="true"
                  style={{ width: 36, height: 36, objectFit: "contain", display: "block" }}
                />
              </div>
              <div>
                <strong>Dirección</strong>
                <div className="muted" style={{ marginTop: ".25rem" }}>
                  Av. Principal 123 y Calle Secundaria, Quito, Ecuador
                </div>
              </div>
            </div>

            <div
              className="contact__item"
              style={{ display: "flex", gap: ".75rem", alignItems: "flex-start" }}
            >
              <div className="contact__icon" aria-hidden>
                <img
                  src={iconPhone}
                  alt=""
                  aria-hidden="true"
                  style={{ width: 36, height: 36, objectFit: "contain", display: "block" }}
                />
              </div>
              <div>
                <strong>Teléfono</strong>
                <div className="muted" style={{ marginTop: ".25rem" }}>
                  <a href="tel:+593991234567">+593 99 123 4567</a>
                </div>
              </div>
            </div>

            <div
              className="contact__item"
              style={{ display: "flex", gap: ".75rem", alignItems: "flex-start" }}
            >
              <div className="contact__icon" aria-hidden>
                <img
                  src={iconEmail}
                  alt=""
                  aria-hidden="true"
                  style={{ width: 36, height: 36, objectFit: "contain", display: "block" }}
                />
              </div>
              <div>
                <strong>Email</strong>
                <div className="muted" style={{ marginTop: ".25rem" }}>
                  <a href="mailto:info@ecuadoraesthetics.com">info@ecuadoraesthetics.com</a>
                </div>
              </div>
            </div>

            <div
              className="contact__map"
              aria-hidden
              style={{
                height: 160,
                borderRadius: "10px",
                background: "linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.04))",
                display: "grid",
                placeItems: "center",
                color: "var(--acero-urbano, #adb5bd)",
              }}
            >
              Espacio para Mapa
            </div>
          </aside>
        </div>
      </div>
    </section>
  );
}

/* ---------------------------
   MultiStepForm (3 pasos + pago)
--------------------------- */
function MultiStepForm() {
  const TOTAL_STEPS = 3;

  // Clínica: horario y reservas de ejemplo (DEMO)
  const CLINIC_HOURS = { start: "09:00", end: "18:00" }; // 9 a 18 hs
  const BOOKED_SLOTS = {
    // YYYY-MM-DD: ["HH:MM", ...]
    // Demo
    "2025-11-10": ["10:00", "15:30"],
    "2025-11-12": ["11:30"],
  };

  const [step, setStep] = useState(1);
  const [submitted, setSubmitted] = useState(false);
  const [showVideo, setShowVideo] = useState(false);
  const [banner, setBanner] = useState(null); // mensaje global de error
  const [touched, setTouched] = useState({}); // para resaltar en rojo
  const [formData, setFormData] = useState({
    nombreCompleto: "",
    email: "",
    codigoArea: "",
    telefono: "",
    procedimiento: "",
    procedimientoOtro: "",
    fechaCita: "",
    horaCita: "", // nuevo
    cirugiasAnteriores: "",
    condicionMedica: "",
    condicionMedicaTipo: "",
    mensajeAdicional: "",
    // pago
    nombreTarjeta: "",
    numeroTarjeta: "",
    expiracion: "",
    cvc: "",
  });

  const procedimientos = [
    "Rinoplastia",
    "Liposucción",
    "Aumento de senos",
    "Abdominoplastia",
    "Lifting facial",
    "Blefaroplastia",
    "Otro",
  ];

  const setField = (k, v) => setFormData((p) => ({ ...p, [k]: v }));

  // Helpers & validaciones
  const digits = (s) => (s || "").replace(/\D/g, "");
  const emailOk = (e) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(e || "");
  const telOk = (area, tel) =>
    /^\d{2,4}$/.test(area || "") && /^\d{7,11}$/.test((tel || "").replace(/\D/g, ""));
  const fechaOk = (f) => {
    if (!f) return false;
    const hoy = new Date();
    hoy.setHours(0, 0, 0, 0);
    const sel = new Date(f);
    sel.setHours(0, 0, 0, 0);
    return sel >= hoy;
  };
  const timeOk = (t) => /^\d{2}:\d{2}$/.test(t || "");
  const withinHours = (t) => t >= CLINIC_HOURS.start && t <= CLINIC_HOURS.end;
  const isSlotBooked = (date, time) => {
    if (!date || !time) return false;
    const arr = BOOKED_SLOTS[date] || [];
    return arr.includes(time);
  };
  const slotError = (() => {
    if (!formData.fechaCita || !formData.horaCita) return null;
    if (!withinHours(formData.horaCita))
      return `Horario fuera de agenda (${CLINIC_HOURS.start}–${CLINIC_HOURS.end} hs).`;
    if (isSlotBooked(formData.fechaCita, formData.horaCita))
      return "Ese horario ya está reservado. Elija otro.";
    return null;
  })();

  const formatCard = (s) => digits(s).replace(/(.{4})/g, "$1 ").trim();
  const expiryOk = (mmYY) => /^(0[1-9]|1[0-2])\/\d{2}$/.test(mmYY || "");

  const step1Valid =
    formData.nombreCompleto.trim() &&
    emailOk(formData.email) &&
    telOk(formData.codigoArea, formData.telefono) &&
    formData.procedimiento &&
    (formData.procedimiento !== "Otro" || formData.procedimientoOtro.trim());

  const step2Valid =
    fechaOk(formData.fechaCita) &&
    timeOk(formData.horaCita) &&
    withinHours(formData.horaCita) &&
    !isSlotBooked(formData.fechaCita, formData.horaCita) &&
    (formData.cirugiasAnteriores === "Si" || formData.cirugiasAnteriores === "No") &&
    (formData.condicionMedica === "Si" || formData.condicionMedica === "No") &&
    (formData.condicionMedica !== "Si" || formData.condicionMedicaTipo.trim());

  const step3Valid =
    formData.nombreTarjeta.trim().length >= 4 &&
    digits(formData.numeroTarjeta).length >= 15 &&
    expiryOk(formData.expiracion) &&
    /^\d{3,4}$/.test(formData.cvc || "");

  const markStepTouched = (n) => {
    if (n === 1) {
      setTouched((t) => ({
        ...t,
        nombreCompleto: true,
        email: true,
        codigoArea: true,
        telefono: true,
        procedimiento: true,
        procedimientoOtro: formData.procedimiento === "Otro",
      }));
    } else if (n === 2) {
      setTouched((t) => ({
        ...t,
        fechaCita: true,
        horaCita: true,
        cirugiasAnteriores: true,
        condicionMedica: true,
        condicionMedicaTipo: formData.condicionMedica === "Si",
      }));
    } else if (n === 3) {
      setTouched((t) => ({
        ...t,
        nombreTarjeta: true,
        numeroTarjeta: true,
        expiracion: true,
        cvc: true,
      }));
    }
  };

  const next = () => {
    setBanner(null);
    if (step === 1 && !step1Valid) {
      markStepTouched(1);
      setBanner("Revisa los campos resaltados para continuar.");
      return;
    }
    if (step === 2 && !step2Valid) {
      markStepTouched(2);
      setBanner(slotError || "Faltan completar datos obligatorios.");
      return;
    }
    setStep(step + 1);
  };

  const back = () => {
    setBanner(null);
    setStep(step - 1);
  };

  const handleSubmit = () => {
    setBanner(null);
    if (!step3Valid) {
      markStepTouched(3);
      setBanner("Completa los datos de pago para finalizar.");
      return;
    }
    console.log("Pagar y enviar (DEMO):", formData);
    setSubmitted(true);
  };

  const progressPct = Math.round((step / TOTAL_STEPS) * 100);

  // Cuando se envía el formulario, lanzar aparición del video
  useEffect(() => {
    let timer;
    if (submitted) {
      timer = setTimeout(() => {
        setShowVideo(true);
      }, 700); // pequeño delay después del check
    } else {
      setShowVideo(false);
    }
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [submitted]);

  if (submitted) {
    const videoContainerStyle = {
      marginTop: "1.5rem",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      position: "relative",
      overflow: "visible",
    };

    const videoStyle = {
      width: "100%",
      maxWidth: "520px",
      borderRadius: "16px",
      overflow: "hidden",
      boxShadow: "var(--shadow, 0 8px 40px rgba(0,0,0,.55))",
      transform: showVideo ? "translateY(0) scale(1)" : "translateY(140px) scale(0.9)",
      opacity: showVideo ? 1 : 0,
      transition:
        "transform 0.7s cubic-bezier(.16,1,.3,1), opacity 0.7s ease-in-out",
      background: "black",
    };

    return (
      <div style={{ padding: "1rem", textAlign: "center" }}>
        <div className="msf-submitted__card">
          <div className="msf-submitted__icon">
            <CheckCircle color="#16a34a" size={40} />
          </div>
          <h3 style={{ marginTop: "0.75rem", marginBottom: "0.25rem" }}>
            ¡Formulario enviado con éxito!
          </h3>
          <p className="muted" style={{ maxWidth: "36rem", margin: "0 auto" }}>
            Gracias por confiar en nosotros. En breve recibirá un correo con los
            detalles de su cita y las indicaciones previas.
          </p>
        </div>

        <div style={videoContainerStyle}>
          <div style={videoStyle}>
            {/* Cambiá la ruta del video por la tuya */}
            <video
              src="/videos/consulta-intro.mp4"
              controls
              style={{ display: "block", width: "100%", height: "auto" }}
            />
          </div>
        </div>

        <p
          className="muted"
          style={{
            marginTop: "0.75rem",
            fontSize: ".85rem",
            opacity: 0.85,
          }}
        >
          Este breve video le explicará qué esperar de su consulta con el Dr. Santillán.
        </p>
      </div>
    );
  }

  return (
    <div className="msf" style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
      <div className="msf__header">
        <div style={{ fontWeight: 700 }}>Paso {step} de {TOTAL_STEPS}</div>
        <div className="muted">
          {step === 1 ? "Información Personal" : step === 2 ? "Detalles de la Cita" : "Pago"}
        </div>
      </div>

      <div className="msf__progress">
        <div className="msf__progress-bar" style={{ width: `${progressPct}%` }} />
      </div>

      {banner && (
        <div className="msf-banner">
          <AlertCircle size={18} />
          <span>{banner}</span>
        </div>
      )}

      <div className="msf-card msf-anim msf-up is-inview">
        {/* PASO 1 */}
        {step === 1 && (
          <div
            className="msf__body"
            style={{ display: "flex", flexDirection: "column", gap: "1rem" }}
          >
            {/* Nombre */}
            <div className="form-group">
              <label className="msf-label">Nombre Completo *</label>
              <div
                className={`msf-field ${
                  touched.nombreCompleto && !formData.nombreCompleto.trim()
                    ? "is-invalid"
                    : ""
                }`}
              >
                <User className="msf-icon" aria-hidden />
                <input
                  className="msf-input"
                  value={formData.nombreCompleto}
                  onChange={(e) => setField("nombreCompleto", e.target.value)}
                  onBlur={() =>
                    setTouched((t) => ({ ...t, nombreCompleto: true }))
                  }
                  placeholder="Juan Pérez"
                />
              </div>
              {touched.nombreCompleto && !formData.nombreCompleto.trim() && (
                <div className="msf-error">Este campo es obligatorio.</div>
              )}
            </div>

            {/* Email */}
            <div className="form-group">
              <label className="msf-label">Email *</label>
              <div
                className={`msf-field ${
                  touched.email && !emailOk(formData.email) ? "is-invalid" : ""
                }`}
              >
                <Mail className="msf-icon" aria-hidden />
                <input
                  className="msf-input"
                  type="email"
                  value={formData.email}
                  onChange={(e) => setField("email", e.target.value)}
                  onBlur={() => setTouched((t) => ({ ...t, email: true }))}
                  placeholder="correo@ejemplo.com"
                />
              </div>
              {touched.email && !emailOk(formData.email) && (
                <div className="msf-error">Ingresa un email válido.</div>
              )}
            </div>

            {/* Teléfono */}
            <div className="form-group">
              <label className="msf-label">Teléfono *</label>
              <div className="msf-row">
                <div
                  className={`msf-field msf-field--sm ${
                    touched.codigoArea &&
                    !/^\d{2,4}$/.test(formData.codigoArea)
                      ? "is-invalid"
                      : ""
                  }`}
                  style={{ flex: "0 0 100px" }}
                >
                  <PhoneIcon className="msf-icon" aria-hidden />
                  <input
                    className="msf-input"
                    value={formData.codigoArea}
                    onChange={(e) =>
                      setField("codigoArea", digits(e.target.value).slice(0, 4))
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, codigoArea: true }))
                    }
                    placeholder="11"
                    inputMode="numeric"
                  />
                </div>
                <div
                  className={`msf-field ${
                    touched.telefono &&
                    !/^\d{7,11}$/.test(digits(formData.telefono))
                      ? "is-invalid"
                      : ""
                  }`}
                  style={{ flex: 1 }}
                >
                  <input
                    className="msf-input"
                    value={formData.telefono}
                    onChange={(e) =>
                      setField("telefono", digits(e.target.value).slice(0, 11))
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, telefono: true }))
                    }
                    placeholder="123456789"
                    inputMode="numeric"
                  />
                </div>
              </div>
              <div className="msf-hint">Ej: 11 12345678</div>
              {touched.codigoArea && !/^\d{2,4}$/.test(formData.codigoArea) && (
                <div className="msf-error">Código de área inválido.</div>
              )}
              {touched.telefono &&
                !/^\d{7,11}$/.test(digits(formData.telefono)) && (
                  <div className="msf-error">Número telefónico inválido.</div>
                )}
            </div>

            {/* Procedimiento */}
            <div className="form-group">
              <label className="msf-label">Procedimiento de Interés *</label>
              <CustomSelect
                icon={<Stethoscope size={18} />}
                options={procedimientos}
                value={formData.procedimiento}
                onChange={(v) => setField("procedimiento", v)}
                placeholder="Selecciona un procedimiento"
              />
              {touched.procedimiento && !formData.procedimiento && (
                <div className="msf-error">Selecciona una opción.</div>
              )}
              {formData.procedimiento === "Otro" && (
                <div style={{ marginTop: 8 }}>
                  <div
                    className={`msf-field ${
                      touched.procedimientoOtro &&
                      !formData.procedimientoOtro.trim()
                        ? "is-invalid"
                        : ""
                    }`}
                  >
                    <input
                      className="msf-input"
                      value={formData.procedimientoOtro}
                      onChange={(e) =>
                        setField("procedimientoOtro", e.target.value)
                      }
                      onBlur={() =>
                        setTouched((t) => ({
                          ...t,
                          procedimientoOtro: true,
                        }))
                      }
                      placeholder="Especifica cuál"
                    />
                  </div>
                  {touched.procedimientoOtro &&
                    !formData.procedimientoOtro.trim() && (
                      <div className="msf-error">
                        Especifica el procedimiento.
                      </div>
                    )}
                </div>
              )}
            </div>

            <button className="boton-formulario" type="button" onClick={next}>
              Siguiente <ArrowRight style={{ marginLeft: 8 }} />
            </button>
          </div>
        )}

        {/* PASO 2 */}
        {step === 2 && (
          <div
            className="msf__body"
            style={{ display: "flex", flexDirection: "column", gap: "1rem" }}
          >
            <div>
              <h3 style={{ margin: 0 }}>Detalles de tu Cita:</h3>
            </div>

            {/* Fecha + Hora lado a lado */}
            <div className="form-row" style={{ gap: ".8rem" }}>
              <div className="form-group" style={{ flex: 1 }}>
                <label className="msf-label">Fecha preferida *</label>
                <div
                  className={`msf-field ${
                    touched.fechaCita && !fechaOk(formData.fechaCita)
                      ? "is-invalid"
                      : ""
                  }`}
                >
                  <Calendar className="msf-icon" aria-hidden />
                  <input
                    className="msf-input"
                    type="date"
                    min={new Date().toISOString().split("T")[0]}
                    value={formData.fechaCita}
                    onChange={(e) => {
                      setField("fechaCita", e.target.value);
                      setField("horaCita", "");
                    }}
                    onBlur={() =>
                      setTouched((t) => ({ ...t, fechaCita: true }))
                    }
                  />
                </div>
                {touched.fechaCita && !fechaOk(formData.fechaCita) && (
                  <div className="msf-error">
                    Elige una fecha válida (hoy o posterior).
                  </div>
                )}
              </div>

              <div className="form-group" style={{ flex: 1 }}>
                <label className="msf-label">Horario de la cita *</label>
                <div
                  className={`msf-field ${
                    touched.horaCita &&
                    (!timeOk(formData.horaCita) || !!slotError)
                      ? "is-invalid"
                      : ""
                  }`}
                >
                  <input
                    className="msf-input"
                    type="time"
                    step={1800} // 30 min
                    min="09:00"
                    max="18:00"
                    value={formData.horaCita}
                    onChange={(e) => setField("horaCita", e.target.value)}
                    onBlur={() =>
                      setTouched((t) => ({ ...t, horaCita: true }))
                    }
                    disabled={!formData.fechaCita}
                  />
                </div>
                {touched.horaCita && !timeOk(formData.horaCita) && (
                  <div className="msf-error">Selecciona un horario válido.</div>
                )}
                {touched.horaCita && !!slotError && (
                  <div className="msf-error">{slotError}</div>
                )}
              </div>
            </div>

            <div className="form-group">
              <label className="msf-label">
                ¿Cirugías plásticas anteriores? *
              </label>
              <div className="switch-row">
                <label
                  className={`switch ${
                    formData.cirugiasAnteriores === "Si" ? "active" : ""
                  }`}
                >
                  <input
                    type="radio"
                    name="cirugiasAnteriores"
                    value="Si"
                    checked={formData.cirugiasAnteriores === "Si"}
                    onChange={(e) =>
                      setField("cirugiasAnteriores", e.target.value)
                    }
                    onBlur={() =>
                      setTouched((t) => ({
                        ...t,
                        cirugiasAnteriores: true,
                      }))
                    }
                  />
                  Sí
                </label>
                <label
                  className={`switch ${
                    formData.cirugiasAnteriores === "No" ? "active" : ""
                  }`}
                >
                  <input
                    type="radio"
                    name="cirugiasAnteriores"
                    value="No"
                    checked={formData.cirugiasAnteriores === "No"}
                    onChange={(e) =>
                      setField("cirugiasAnteriores", e.target.value)
                    }
                    onBlur={() =>
                      setTouched((t) => ({
                        ...t,
                        cirugiasAnteriores: true,
                      }))
                    }
                  />
                  No
                </label>
              </div>
              {touched.cirugiasAnteriores && !formData.cirugiasAnteriores && (
                <div className="msf-error">Selecciona una opción.</div>
              )}
            </div>

            <div className="form-group">
              <label className="msf-label">
                ¿Padeces alguna condición médica? *
              </label>
              <div className="switch-row">
                <label
                  className={`switch ${
                    formData.condicionMedica === "Si" ? "active" : ""
                  }`}
                >
                  <input
                    type="radio"
                    name="condicionMedica"
                    value="Si"
                    checked={formData.condicionMedica === "Si"}
                    onChange={(e) =>
                      setField("condicionMedica", e.target.value)
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, condicionMedica: true }))
                    }
                  />
                  Sí
                </label>
                <label
                  className={`switch ${
                    formData.condicionMedica === "No" ? "active" : ""
                  }`}
                >
                  <input
                    type="radio"
                    name="condicionMedica"
                    value="No"
                    checked={formData.condicionMedica === "No"}
                    onChange={(e) =>
                      setField("condicionMedica", e.target.value)
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, condicionMedica: true }))
                    }
                  />
                  No
                </label>
              </div>

              {formData.condicionMedica === "Si" && (
                <div style={{ marginTop: 8 }}>
                  <div
                    className={`msf-field ${
                      touched.condicionMedicaTipo &&
                      !formData.condicionMedicaTipo.trim()
                        ? "is-invalid"
                        : ""
                    }`}
                  >
                    <input
                      className="msf-input"
                      value={formData.condicionMedicaTipo}
                      onChange={(e) =>
                        setField("condicionMedicaTipo", e.target.value)
                      }
                      onBlur={() =>
                        setTouched((t) => ({
                          ...t,
                          condicionMedicaTipo: true,
                        }))
                      }
                      placeholder="Describe tu condición"
                    />
                  </div>
                  {touched.condicionMedicaTipo &&
                    !formData.condicionMedicaTipo.trim() && (
                      <div className="msf-error">
                        Describe brevemente tu condición.
                      </div>
                    )}
                </div>
              )}
              {touched.condicionMedica && !formData.condicionMedica && (
                <div className="msf-error">Selecciona una opción.</div>
              )}
            </div>

            <div className="form-group">
              <label className="msf-label">Mensaje adicional</label>
              <div className="msf-field">
                <textarea
                  className="msf-textarea"
                  rows={4}
                  value={formData.mensajeAdicional}
                  onChange={(e) =>
                    setField("mensajeAdicional", e.target.value)
                  }
                  placeholder="Opcional…"
                />
              </div>
            </div>

            <div className="msf-row" style={{ gap: ".6rem" }}>
              <button className="Boton_anterior" type="button" onClick={back}>
                <ArrowLeft style={{ marginRight: 8 }} /> Anterior
              </button>
              <button className="boton-formulario" type="button" onClick={next}>
                Confirma tu Agenda <ArrowRight style={{ marginLeft: 8 }} />
              </button>
            </div>
          </div>
        )}

        {/* PASO 3 – PAGO */}
        {step === 3 && (
          <div
            className="msf__body"
            style={{ display: "flex", flexDirection: "column", gap: "1rem" }}
          >
            <div className="pay-head">
              <span className="pay-chip">
                <CreditCard size={16} /> Pago Seguro
              </span>
              <h3 style={{ margin: "8px 0 0" }}>Confirmar reserva</h3>
            </div>

            <div className="pay-grid">
              <div className="form-group">
                <label className="msf-label">Nombre en la tarjeta *</label>
                <div
                  className={`msf-field ${
                    touched.nombreTarjeta && !formData.nombreTarjeta.trim()
                      ? "is-invalid"
                      : ""
                  }`}
                >
                  <input
                    className="msf-input"
                    value={formData.nombreTarjeta}
                    onChange={(e) =>
                      setField("nombreTarjeta", e.target.value)
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, nombreTarjeta: true }))
                    }
                    placeholder="Como figura en la tarjeta"
                  />
                </div>
                {touched.nombreTarjeta && !formData.nombreTarjeta.trim() && (
                  <div className="msf-error">Este campo es obligatorio.</div>
                )}
              </div>

              <div className="form-group">
                <label className="msf-label">Número de tarjeta *</label>
                <div
                  className={`msf-field ${
                    touched.numeroTarjeta &&
                    digits(formData.numeroTarjeta).length < 15
                      ? "is-invalid"
                      : ""
                  }`}
                >
                  <input
                    className="msf-input"
                    inputMode="numeric"
                    maxLength={19}
                    value={formData.numeroTarjeta}
                    onChange={(e) =>
                      setField("numeroTarjeta", formatCard(e.target.value))
                    }
                    onBlur={() =>
                      setTouched((t) => ({ ...t, numeroTarjeta: true }))
                    }
                    placeholder="1234 5678 9012 3456"
                  />
                </div>
                {touched.numeroTarjeta &&
                  digits(formData.numeroTarjeta).length < 15 && (
                    <div className="msf-error">
                      Número de tarjeta inválido.
                    </div>
                  )}
              </div>

              <div className="form-row">
                <div className="form-group" style={{ flex: 1 }}>
                  <label className="msf-label">Expiración (MM/AA) *</label>
                  <div
                    className={`msf-field ${
                      touched.expiracion && !expiryOk(formData.expiracion)
                        ? "is-invalid"
                        : ""
                    }`}
                  >
                    <input
                      className="msf-input"
                      inputMode="numeric"
                      maxLength={5}
                      value={formData.expiracion}
                      onChange={(e) => {
                        let v = e.target.value.replace(/\D/g, "").slice(0, 4);
                        if (v.length >= 3) v = `${v.slice(0, 2)}/${v.slice(2)}`;
                        setField("expiracion", v);
                      }}
                      onBlur={() =>
                        setTouched((t) => ({ ...t, expiracion: true }))
                      }
                      placeholder="MM/AA"
                    />
                  </div>
                  {touched.expiracion && !expiryOk(formData.expiracion) && (
                    <div className="msf-error">
                      Formato inválido. Usa MM/AA.
                    </div>
                  )}
                </div>
                <div className="form-group" style={{ flex: 1 }}>
                  <label className="msf-label">CVC *</label>
                  <div
                    className={`msf-field ${
                      touched.cvc && !/^\d{3,4}$/.test(formData.cvc)
                        ? "is-invalid"
                        : ""
                    }`}
                  >
                    <input
                      className="msf-input"
                      inputMode="numeric"
                      maxLength={4}
                      value={formData.cvc}
                      onChange={(e) =>
                        setField("cvc", digits(e.target.value).slice(0, 4))
                      }
                      onBlur={() => setTouched((t) => ({ ...t, cvc: true }))}
                      placeholder="3 o 4 dígitos"
                    />
                  </div>
                  {touched.cvc && !/^\d{3,4}$/.test(formData.cvc) && (
                    <div className="msf-error">CVC inválido.</div>
                  )}
                </div>
              </div>
            </div>

            <div className="msf-row" style={{ gap: ".6rem" }}>
              <button className="Boton_anterior" type="button" onClick={back}>
                <ArrowLeft style={{ marginRight: 8 }} /> Anterior
              </button>
              <button className="Boton_Enviar" type="button" onClick={handleSubmit}>
                Pagar y Enviar <CheckCircle style={{ marginLeft: 8 }} />
              </button>
            </div>

            <p className="muted" style={{ fontSize: ".8rem", marginTop: 6 }}>
              * Demostración. Integra tu pasarela (Stripe/MercadoPago/n8n) en el submit real.
            </p>
          </div>
        )}
      </div>
    </div>
  );
}

/* ----- Dropdown accesible UL/LI con icono ----- */
function CustomSelect({ icon, options, value, onChange, placeholder = "Selecciona…" }) {
  const [open, setOpen] = useState(false);
  const [focus, setFocus] = useState(-1);

  const toggle = () => setOpen((o) => !o);
  const close = () => {
    setOpen(false);
    setFocus(-1);
  };

  const onKeyDown = (e) => {
    if (!open && (e.key === "Enter" || e.key === " " || e.key === "ArrowDown")) {
      e.preventDefault();
      setOpen(true);
      setFocus(0);
      return;
    }
    if (!open) return;
    if (e.key === "Escape") {
      e.preventDefault();
      close();
      return;
    }
    if (e.key === "ArrowDown") {
      e.preventDefault();
      setFocus((i) => Math.min(i + 1, options.length - 1));
      return;
    }
    if (e.key === "ArrowUp") {
      e.preventDefault();
      setFocus((i) => Math.max(i - 1, 0));
      return;
    }
    if (e.key === "Enter") {
      e.preventDefault();
      const pick = options[focus];
      if (pick) {
        onChange(pick);
        close();
      }
    }
  };

  return (
    <div className="proc-select" onKeyDown={onKeyDown}>
      <button
        type="button"
        className="proc-select__btn msf-input"
        aria-expanded={open}
        aria-haspopup="listbox"
        onClick={toggle}
      >
        <span className="proc-select__icon">{icon}</span>
        <span className={`proc-select__value ${value ? "" : "is-placeholder"}`}>
          {value || placeholder}
        </span>
        <span className={`proc-select__caret ${open ? "is-open" : ""}`} aria-hidden>
          ▾
        </span>
      </button>

      {open && (
        <ul className="proc-select__list" role="listbox">
          {options.map((opt, i) => {
            const active = value === opt;
            return (
              <li
                key={opt}
                role="option"
                aria-selected={active}
                className={`proc-select__option ${active ? "is-active" : ""} ${
                  i === focus ? "is-focus" : ""
                }`}
                onMouseEnter={() => setFocus(i)}
                onMouseDown={(e) => {
                  e.preventDefault();
                  onChange(opt);
                  close();
                }}
              >
                <span>{opt}</span>
                {active && <span className="proc-select__check">✓</span>}
              </li>
            );
          })}
        </ul>
      )}
    </div>
  );
}
